body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  padding: 0;
  margin: 0;
}
body {
  background-color: rgb(243, 243, 243);
  height: 100vh;
  width: 100vw;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.img {
  width: 20px 
}
.img img {
  width: 100%
}

#indicator {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  background-color: #fff;
  text-align: center;
  border: 1px solid #ddd;
  width: 300px;
  height: 300px;
  border-radius: 4px;
  box-sizing: border-box;
  box-shadow: 0 15px 35px rgba(50,50,93,.1), 0 5px 15px rgba(0,0,0,.07);
}

#indicator h1 {
  font-size: 7vw;
}

#indicator #result {
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
#indicator #links {
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  display: -webkit-flex;
  display: flex;
  position: relative;
}
#indicator #links a {
  -webkit-flex: 1 1;
          flex: 1 1;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  padding: 15px 0px;
  cursor: pointer;
  color: black;
  text-decoration: none;
}
#indicator #links a:hover {
  background-color: rgb(243, 243, 243);
  color: black;
}
#indicator #links a:visited, #indicator #links a:active {
  color: black;
  text-decoration: none;
}

#indicator #links a:last-of-type {
  border-left: 1px solid #ddd;
}

#indicator #links .sdk{
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  display: -webkit-flex;
  display: flex;
}

#indicator #links #refresher {
  box-sizing: border-box;
  position: absolute;
  padding: 0px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 1px solid #ddd;
  background-color: #fff;
  top: 0%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  
}
#indicator #links #refresher:hover {
  background-color: rgb(243, 243, 243);
  cursor: pointer;
}

.loading {
  -webkit-transform-origin: center center;
          transform-origin: center center;
  -webkit-animation: App-logo-spin infinite 1s linear;
          animation: App-logo-spin infinite 1s linear;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg)
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg)
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg)
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg)
  }
}

